import './Contact.css';
import React from 'react';
import {
    Modal,Button
} from 'react-bootstrap';
function MyVerticallyCenteredModal(props) {
    return (
      <Modal
        {...props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title id="contained-modal-title-vcenter">
            ESTUDIOS
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p>
          <ul>
 <li><h4 className="textno">Colegio Pedagógico Dulce María</h4><h5 className="textnon">2009-2020</h5>En esta institucion educativa culmine mis estudios escolares nombrandome como bachiller academico completando un gran ciclo en mi vida</li>
 <li><h4 className="textno">SENA</h4><h5 className="textnon">Actualidad</h5>En la institucion educativa conoida como SENA (servicio nacional de aprendizaje) culmine mi formacion como tecnico en programación de software y ahora mismo estoy en la formación tecnologa de ADSI (Analisis y desarrollo de sistemas de información)</li>
 
</ul>
           </p>
        </Modal.Body>
        <Modal.Footer>
          <Button onClick={props.onHide}>volver</Button>
        </Modal.Footer>
      </Modal>
    );
  }


